<template>
  <div class="container" @scroll="add">
    <div class="header">
      <div class="header_header">
        <h2>网盘检索</h2>
        <h5>网盘资源检索</h5>
      </div>
    </div>
    <div class="header_search">
      <input
        type="text"
        placeholder="输入内容，回车搜索"
        v-model.lazy="netdisk_searchKey"
      />
    </div>
    <div class="main">
      <ul>
        <li
          class="searchContent"
          v-show="flag2"
          v-for="(m, i) in filterList"
          :key="i + 's'"
        >
          <h3>
            <span><img :src="m.img" :title="m.name" /></span>{{ m.name }}
          </h3>
          <hr />
          <div>
            <span>{{ m.detail }}</span>
          </div>
          <div>
            <a target="_blank" :href="m.url">点击进入</a>
          </div>
        </li>
        <li
          v-show="flag"
          v-for="(n, i) in $store.state.netdiskContent.netdiskList.slice(
            start,
            end
          )"
          :key="i"
        >
          <h3>
            <span><img :src="n.img" :title="n.name" /></span>{{ n.name }}
          </h3>
          <hr />
          <div>
            <span>{{ n.detail }}</span>
          </div>
          <div>
            <a target="_blank" :href="n.url">点击进入</a>
          </div>
        </li>
      </ul>
      <div class="moreContent" v-show="hiddenBtn_more">
        <a>加载中...</a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "netdiskContent",
  data() {
    return {
      flag: true,
      flag2: false,
      hiddenBtn_more: true,
      start: 0,
      end: 10,
      netdisk_searchKey: "",
      filterList: [],
    };
  },
  methods: {
     add() {
      // 储存this
      let _this = this;
      let timer = null;
      const container = document.querySelector(".container");
      const moreContent = document.querySelector(".moreContent");
      const moreContent_a = moreContent.querySelector('a')
      const containerHeight = container.clientHeight;
      // 获取距离
      // console.log(containerHeight);
      // console.log(top);

      function eventFun() {
        timer = null;
        if (timer !== null) {
          clearTimeout(timer);
        } else {
          timer = setTimeout(() => {
            let top = moreContent.getBoundingClientRect().top;
            if (containerHeight >= top) {
              // console.log("到底了");
              _this.end += 10;
              // console.log(_this);
              timer = !timer;
              // console.log(timer);
            }
          }, 700);
          if(_this.end >= _this.$store.state.netdiskContent.netdiskList.length){
            moreContent_a.innerText = '没有更多内容了'
          }
        }
      }
      eventFun();
    },
  },
  watch: {
    netdisk_searchKey: {
      // 首次绑定是否执行 handler
      // immediate:true,
      handler(val) {
        // 输入框输入时替换列表
        if (this.netdisk_SearchKey != "") {
          this.flag = false;
          this.flag2 = true;
          this.hiddenBtn_more=false

          // 过滤数组赋值给新数组
          this.filterList = this.$store.state.netdiskContent.netdiskList.filter(
            (item) => {
            // 判断是否在数组中存在|| item.name.toLowerCase().indexOf(val) !== -1
            // return item.name.indexOf(val) !== -1
            let nameUpper = item.name.toUpperCase()
            let nameLower = item.name.toLowerCase()
            if (item.detail.indexOf(val) !== -1 || nameUpper.indexOf(val) !== -1 || nameLower.indexOf(val) !==-1) {
              return item;
              // console.log(item);
            }
          }
          );
        } else {
          this.flag = true;
          this.flag2 = false;
          this.hiddenBtn_more=true
        }
      },
    },
  },
};
</script>